.songs__section .title__background {
  background-image: url("../images/songs__image.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100%;
  background-blend-mode: darken;
  background-color: rgba(0, 0, 0, 0.747);
  text-align: center;
  padding: 70px 0;
  background-attachment: fixed;
}

.songs__section .title__background .songs__title {
  font-size: 48px;
  letter-spacing: 2px;
  position: relative;
}

.songs__section .title__background .songs__title::after {
  content: "";
  position: absolute;
  top: 130%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--whiteColor);
  width: 70px;
  height: 2px;
}

.songs__section .title__background .songs__about {
  font-size: 23px;
  letter-spacing: 2px;
  margin-top: 35px;
}

.songs__section .songs__items {
  padding-top: 20px;
  overflow-y: scroll;
  margin: 25px auto auto auto;
  max-width: 1110px;
  height: 400px;
  padding: 10px;
}

.songs__section .songs__items::-webkit-scrollbar {
  width: 10px;
}

.songs__section .songs__items::-webkit-scrollbar-thumb {
  background-color: var(--mainColor);
  border-radius: 20px;
}

.songs__section .songs__items .songs__item {
  border-bottom: 1px solid rgba(255, 255, 255, 0.308);
}

.songs__section .songs__items .songs__item .beat__name {
  margin-left: 10px;
  font-size: 20px;
  letter-spacing: 1px;
}

.songs__section .songs__items .songs__item .music_mp3 {
  width: 100%;
  border: none;
  margin: 30px 0;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-panel {
  background-color: #202020;
  display: flex;
  gap: 0 7px;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-play-button {
  background-color: #56c7ff;
  border-radius: 50%;
  transition: 0.2s ease;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-play-button:hover {
  background-color: #ffffff;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-current-time-display {
  color: #fff;
  font-size: 18px;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-time-remaining-display {
  color: #fff;
  font-size: 18px;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-timeline {
  background-color: #56c7ff;
  border-radius: 50px;
  transition: 0.2s ease;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-timeline:hover {
  background-color: #ffffff;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-volume-slider {
  background-color: rgba(189, 189, 189, 0.712);
  border-radius: 25px;
  margin-right: 5px;
  transition: 0.2s ease;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-volume-slider:hover {
  background-color: #ffffff;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-mute-button {
  background-color: #56c7ff;
  border-radius: 50%;
  transition: 0.2s ease;
}

.songs__section .songs__items .songs__item .music_mp3::-webkit-media-controls-mute-button:hover {
  background-color: #ffffff;
}

.images__section {
  padding-top: 50px;
}

.images__section .title__background {
  background-image: url("../images/images__section_photo.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  text-align: center;
  min-height: 100%;
  padding: 70px 0;
  background-blend-mode: darken;
  background-color: rgba(0, 0, 0, 0.493);
}

.images__section .title__background .images__title {
  font-size: 48px;
  letter-spacing: 2px;
  position: relative;
}

.images__section .title__background .images__title::after {
  content: "";
  position: absolute;
  top: 130%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--whiteColor);
  width: 70px;
  height: 2px;
}

.images__section .title__background .images__about {
  font-size: 23px;
  letter-spacing: 2px;
  margin-top: 35px;
}

.images__section .images__items {
  padding-top: 50px;
}

.images__section .images__items .images_item {
  max-width: 800px;
  border-radius: 20px;
  position: relative;
}

.images__section .images__items .images_item .img_item {
  width: 100%;
  border-radius: 20px;
}

.images__section .images__items .images_item .download_icon {
  position: absolute;
  top: 85%;
  left: 45%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  transition: 0.2s ease;
}

.images__section .images__items .images_item .download_icon:hover {
  background-color: transparent;
}

.images__section .images__items .images_item .download_icon .download_link .download_img {
  width: 50px;
}

.images__section .images__items .images_item .enlarge_icon {
  position: absolute;
  top: 85%;
  left: 55%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  transition: 0.2s ease;
}

.images__section .images__items .images_item .enlarge_icon:hover {
  background-color: transparent;
}

.images__section .images__items .images_item .enlarge_icon .elarge_link .elarge_img {
  width: 50px;
}

.contact__section {
  padding-top: 50px;
}

.contact__section .title__background {
  background-image: url("../images/contact_bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100%;
  background-blend-mode: darken;
  background-color: rgba(0, 0, 0, 0.747);
  text-align: center;
  padding: 70px 0;
  background-attachment: fixed;
}

.contact__section .title__background .contact__title {
  font-size: 48px;
  letter-spacing: 2px;
  position: relative;
}

.contact__section .title__background .contact__title::after {
  content: "";
  position: absolute;
  top: 130%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--whiteColor);
  width: 70px;
  height: 2px;
}

.contact__section .title__background .contact__about {
  margin-top: 35px;
  letter-spacing: 2px;
  font-size: 23px;
}

.contact__section .contact__items {
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
  gap: 0 10px;
}

.contact__section .contact__items .contact__info .contact_info_title {
  font-size: 23px;
  letter-spacing: 2px;
}

.contact__section .contact__items .contact__info .info__item {
  margin-top: 20px;
}

.contact__section .contact__items .contact__info .info__item .mail__icon {
  display: flex;
  gap: 0 10px;
}

.contact__section .contact__items .contact__info .info__item .mail__icon .mail_img {
  width: 25px;
  margin-bottom: 15px;
}

.contact__section .contact__items .contact__info .info__item .mail__icon .mail_link {
  color: var(--mainColor);
  font-size: 20px;
  letter-spacing: 1px;
  transition: 0.2s ease;
}

.contact__section .contact__items .contact__info .info__item .mail__icon .mail_link:hover {
  color: var(--whiteColor);
}

.contact__section .contact__items .contact__info .info__item .phone__icon {
  display: flex;
  gap: 0 10px;
}

.contact__section .contact__items .contact__info .info__item .phone__icon .phone_img {
  width: 25px;
}

.contact__section .contact__items .contact__info .info__item .phone__icon .phone_link {
  color: var(--mainColor);
  font-size: 20px;
  letter-spacing: 1px;
  transition: 0.2s ease;
}

.contact__section .contact__items .contact__info .info__item .phone__icon .phone_link:hover {
  color: var(--whiteColor);
}

.contact__section .contact__items .contact__info .social__networks {
  margin-top: 40px;
}

.contact__section .contact__items .contact__info .social__networks .social__network {
  margin-top: 20px;
}

.contact__section .contact__items .contact__info .social__networks .social__network .social__link {
  display: flex;
  gap: 0 10px;
  color: var(--mainColor);
  transition: 0.2s ease;
  letter-spacing: 1px;
  font-size: 20px;
}

.contact__section .contact__items .contact__info .social__networks .social__network .social__link:hover {
  color: var(--whiteColor);
}

.contact__section .contact__items .contact__info .social__networks .social__network .social__link .social_icon .social_img {
  width: 25px;
}

.contact__section .contact__items .contact__image {
  max-width: 800px;
}

.contact__section .contact__items .contact__image .contact__img {
  width: 100%;
  border-radius: 25px;
}

@media screen and (max-width: 870px) {
  .contact__section .contact__items {
    flex-direction: column;
    gap: 10px 0;
  }
}

@media screen and (max-width: 768px) {
  .images__section .images__items .images_item .download_icon .download_link .download_img {
    width: 40px;
  }

  .images__section .images__items .images_item .enlarge_icon .elarge_link .elarge_img {
    width: 40px;
  }
}

@media screen and (max-width: 540px) {
  .images__section .images__items .images_item .download_icon {
    border-radius: 5px;
  }

  .images__section .images__items .images_item .download_icon .download_link .download_img {
    width: 30px;
  }

  .images__section .images__items .images_item .enlarge_icon {
    border-radius: 5px;
  }

  .images__section .images__items .images_item .enlarge_icon .elarge_link .elarge_img {
    width: 30px;
  }
}

@media screen and (max-width: 420px) {
  .songs__section .songs__items .songs__item .beat__name {
    font-size: 18px;
  }

  .images__section .images__items .images_item .download_icon {
    left: 43%;
  }

  .contact__section .contact__items .contact__info .info__item .mail__icon .mail_link {
    font-size: 18px;
  }

  .contact__section .contact__items .contact__info .info__item .phone__icon .phone_link {
    font-size: 18px;
  }

  .contact__section .contact__items .contact__info .social__networks .social__network .social__link {
    font-size: 18px;
  }

  .contact__section .contact__items .contact__image .contact__img {
    border-radius: 15px;
  }
}

@media screen and (max-width: 320px) {
  .images__section .images__items .images_item .download_icon .download_link .download_img {
    width: 25px;
  }

  .images__section .images__items .images_item .enlarge_icon .elarge_link .elarge_img {
    width: 25px;
  }
}