header .header__section {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 80px;
  transition: 0.2s ease;
  z-index: 99999;
}

header .header__section.navBg {
  background-color: #202020;
  height: 70px;
}

header .header__section.navBg .header__navbar {
  height: 70px;
}

header .header__section .header__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  transition: 0.2s ease;
}

header .header__section .header__navbar .logo .logo_name {
  font-size: 32px;
  color: var(--whiteColor);
  font-family: var(--edusabeginnerbold);
}

header .header__section .header__navbar .logo .logo_name svg {
  width: 32px;
}

header .header__section .header__navbar .logo .logo_name::selection {
  background: none;
}

header .header__section .header__navbar .navside .navClose {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  padding: 13px 10px;
}

header .header__section .header__navbar .navside .navClose .closeBar1 {
  width: 30px;
  height: 5px;
  background-color: #fff;
  transform: translate(0, 3px) rotate(45deg);
}

header .header__section .header__navbar .navside .navClose .closeBar2 {
  width: 30px;
  height: 5px;
  background-color: #fff;
  transform: translate(0px, -2px) rotate(-45deg);
}

header .header__section .header__navbar .navside .nav_links .links {
  display: flex;
}

header .header__section .header__navbar .navside .nav_links .links .list .link {
  font-size: 23px;
  letter-spacing: 1.5px;
  color: var(--whiteColor);
  padding: 20px;
  transition: 0.2s ease;
  position: relative;
}

header .header__section .header__navbar .navside .nav_links .links .list .link::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%) rotate(90deg);
  background-color: var(--mainColor);
  width: 0;
  height: 2px;
  transition: 0.2s ease;
}

header .header__section .header__navbar .navside .nav_links .links .list .link:hover {
  color: var(--mainColor);
}

header .header__section .header__navbar .navside .nav_links .links .list .link:hover::after {
  width: 25px;
}

header .header__section .header__navbar .navside .nav_links .links .list .link.link-active {
  color: var(--mainColor);
}

header .header__section .header__navbar .navside .nav_links .links .list .link.link-active::after {
  width: 25px;
}

header .header__section .header__navbar .burger {
  cursor: pointer;
  padding: 3px 10px;
}

header .header__section .header__navbar .burger .bar {
  margin: 6px 0;
  width: 30px;
  height: 4px;
  background-color: #fff;
}

@media screen and (min-width: 769px) {
  header .header__section .header__navbar .navside .navClose {
    display: none;
  }

  header .header__section .header__navbar .burger {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  header .header__section .header__navbar .navside {
    position: absolute;
    top: 0;
    left: 120%;
    transform: translateX(-50%);
    width: 0;
    background-color: rgba(0, 0, 0, 0.9450980392);
    padding: 0;
    transition: 0.2s ease;
  }

  header .header__section .header__navbar .navside.activeMenu {
    left: 50%;
    padding: 120px 25px;
    width: 100%;
  }

  header .header__section .header__navbar .navside .nav_links .links {
    display: flex;
    flex-direction: column;
    text-align: end;
  }

  header .header__section .header__navbar .navside .nav_links .links .list .link {
    font-size: 23px;
    letter-spacing: 1.5px;
    color: var(--whiteColor);
    display: block;
    padding: 5px 0;
    transition: 0.2s ease;
    width: 100%;
    position: relative;
  }

  header .header__section .header__navbar .navside .nav_links .links .list .link::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 100%;
    transform: translateY(-50%) rotate(90deg);
    background-color: var(--mainColor);
    width: 0;
    height: 2px;
    transition: 0.2s ease;
  }

  header .header__section .header__navbar .navside .nav_links .links .list .link:hover {
    color: var(--mainColor);
  }

  header .header__section .header__navbar .navside .nav_links .links .list .link:hover::after {
    width: 25px;
  }

  header .header__section .header__navbar .navside .nav_links .links .list .link.link-active {
    color: var(--mainColor);
  }

  header .header__section .header__navbar .navside .nav_links .links .list .link.link-active::after {
    width: 25px;
  }
}