header {
  background-image: url("../images/home__photo.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100%;
  background-blend-mode: darken;
  background-color: rgba(0, 0, 0, 0.493);
}

header .header__info {
  padding-top: 230px;
}

header .header__info .header__title {
  max-width: 470px;
  font-size: 48px;
  margin: 0 auto;
}

header .header__info .header__text {
  color: var(--whiteColor);
  font-size: 23px;
  max-width: 470px;
  margin: 0 auto;
  padding-top: 20px;
  line-height: 37px;
}

header .header__btn {
  max-width: 470px;
  margin: 30px auto;
}

header .header__btn .btn {
  border: 2px solid var(--mainColor);
  color: var(--whiteColor);
  border-radius: 50px;
  padding: 10px 25px;
  font-size: 23px;
  transition: 0.3s ease;
}

header .header__btn .btn:hover {
  background-color: var(--mainColor);
}

header .my_social_networks {
  display: flex;
  justify-content: flex-end;
  gap: 0 10px;
  margin-top: 200px;
  padding: 10px 0;
}

header .my_social_networks .network {
  max-width: 25px;
  transition: 0.2s ease;
}

header .my_social_networks .network:hover {
  transform: translateY(-5px);
}

header .my_social_networks .network .network_link .network_img {
  width: 100%;
}

@media screen and (max-width: 420px) {
  header .header__info .header__text {
    font-size: 20px;
    line-height: 30px;
  }

  header .header__btn .btn {
    font-size: 20px;
    padding: 8px 23px;
  }
}