.footer__section {
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  margin-top: 70px;
}

.footer__section .copyright {
  text-align: center;
  padding: 30px 0;
}

.footer__section .copyright .developers__name {
  font-size: 16px;
  letter-spacing: 2px;
}

.footer__section .copyright .developers__name .name_link {
  font-size: 20px;
  letter-spacing: 2px;
  color: var(--whiteColor);
  transition: 0.2s ease;
}

.footer__section .copyright .developers__name .name_link:hover {
  color: var(--hoverColor);
}

.footer__section .copyright .rights {
  margin-top: 10px;
  letter-spacing: 2px;
  font-size: 18px;
}